    footerMethod({ columns, data }) {
      if (this.viewNameMX == "") {
        var DJ = "";
        var SL = "";
        var numberId = this.numberId;
        for (var i = 0; i < data.length; i++) {
          if (data[i].DJ == "") {
            DJ = 0;
          } else {
            DJ = parseInt(data[i].DJ);
          }
          if (data[i].SL == "") {
            SL = 0;
          } else {
            SL = parseInt(data[i].SL);
          }

          data[i].ZJ = DJ * SL;
        }
        return [
          columns.map((column, columnIndex) => {
            if (columnIndex === 0) {
              return "";
            }
            if (["SL", "ZJ"].includes(column.property)) {
              return XEUtils.sum(data, column.property);
            }
            return "-";
          })
        ];
      } else if (this.viewNameMX == "") {
        var DJ = "";
        var SL = "";
        var ZJ = "";
        var ZKL;
        var ZKJE;
        var numberId = this.numberId;
        for (var i = 0; i < data.length; i++) {
          if (data[i].DJ == "") {
            DJ = 0;
          } else {
            DJ = parseInt(data[i].DJ);
          }
          if (data[i].SL == "") {
            SL = 0;
          } else {
            SL = parseInt(data[i].SL);
          }
          ZKL = data[i].ZKL;
          //
          ZJ = DJ * SL; //=*
          data[i].ZJ = ZJ; //
          if (ZJ > 0) {
            //
            data[i].ZKJ = ZJ * ZKL; //=*
            //
            ZKJE = ZJ - ZJ * ZKL; //-
            var ZKJEZJ = ZKJE.toFixed(1); //1
            data[i].ZKJE = ZKJEZJ; //
          }
        }
        return [
          columns.map((column, columnIndex) => {
            if (columnIndex === 0) {
              return "";
            }
            if (["SL", "ZJ", "ZKJE"].includes(column.property)) {
              return XEUtils.sum(data, column.property);
            }
            return "-";
          })
        ];
      } else if (this.viewNameMX == "") {
        var DWLFH = "";
        var MJ = "";
        var XZXS = "";
        var LFH = "";
        var NJLL = "";
        var NJSL = "";
        for (var i = 0; i < data.length; i++) {
          if (data[i].DWLFH == "") {
            DWLFH = 0;
          } else {
            DWLFH = parseInt(data[i].DWLFH);
          }
          if (data[i].MJ == "") {
            MJ = 0;
          } else {
            MJ = parseInt(data[i].MJ);
          }
          if (data[i].XZXS == "") {
            XZXS = 0;
          } else {
            XZXS = parseInt(data[i].XZXS);
          }
          if (data[i].NJLL == "") {
            NJLL = 0;
          } else {
            NJLL = parseInt(data[i].NJLL);
          }
          if (data[i].NJSL == "") {
            NJSL = 0;
          } else {
            NJSL = parseInt(data[i].NJSL);
          }
          data[i].LFH = DWLFH * MJ * XZXS;
          data[i].NJLL = NJSL * NJLL;
        }
        return [
          columns.map((column, columnIndex) => {
            if (columnIndex === 0) {
              return "";
            }
            if (["MJ", "LFH", "NJSL", "NJLL"].includes(column.property)) {
              return XEUtils.sum(data, column.property);
            }
            return "-";
          })
        ];
      } else if (this.viewNameMX == "") {
        var DWRFH = "";
        var MJ = "";
        var XZXS = "";
        for (var i = 0; i < data.length; i++) {
          if (data[i].DWRFH == "") {
            DWRFH = 0;
          } else {
            DWRFH = parseInt(data[i].DWRFH);
          }
          if (data[i].MJ == "") {
            MJ = 0;
          } else {
            MJ = parseInt(data[i].MJ);
          }
          if (data[i].XZXS == "") {
            XZXS = 0;
          } else {
            XZXS = parseInt(data[i].XZXS);
          }
          data[i].RFH = DWRFH * MJ * XZXS;
        }
        return [
          columns.map((column, columnIndex) => {
            if (columnIndex === 0) {
              return "";
            }
            if (["MJ", "RFH"].includes(column.property)) {
              return XEUtils.sum(data, column.property);
            }
            return "-";
          })
        ];
      }
