 <template>
  <el-form :model="passForm" status-icon ref="passForm" label-width="120px">
    <div class="overallLayout">
      <div class="type">
        <div class="navigationBlock"></div>
      </div>
      <div class="border"></div>
    </div>

    <el-form-item label="" prop="oldPass">
      <el-input
        :type="passForm.show.old?'text':'password'"
        v-model="passForm.oldPass"
        placeholder=""
        class="width"
      ></el-input>
    </el-form-item>
    <el-form-item label="" prop="newPass">
      <el-input
        :type="passForm.show.new?'text':'password'"
        v-model="passForm.newPass"
        placeholder=""
        class="width"
      ></el-input>
    </el-form-item>
    <el-form-item label="" prop="checkPass">
      <el-input
        :type="passForm.show.check?'text':'password'"
        v-model="passForm.checkPass"
        placeholder=""
        class="width"
      ></el-input>
    </el-form-item>

    <el-form-item>
      <el-button type="primary" @click="confirm()"></el-button>
      <el-button @click="cancel()"></el-button>
    </el-form-item>
  </el-form>
</template>

 <script>
import Axios from "Axios";

import $ from "jquery";
export default {
  data() {
    return {
      passForm: {
        oldPass: "",
        newPass: "",
        checkPass: "",
        show: {
          old: false,
          new: false,
          check: false
        }
      },
      user: sessionStorage.getItem("curusercode")
    };
  },
  computed: {
    updata: {
      get: function() {
        //get 
        return this.updata;
      },
      // set  infpdata
      set: function(res) {
        if (res.code == "error") {
          this.$alert(res.msg, {
            confirmButtonText: ""
          });
        } else {
          this.$alert(res.msg, {
            confirmButtonText: ""
          });
        }
      }
    }
  },
  methods: {
    //
    confirm() {
      if (this.passForm.newPass != this.passForm.checkPass) {
        this.$alert("", {
          confirmButtonText: ""
        });
      } else if (this.passForm.oldPass == "") {
        this.$alert("", {
          confirmButtonText: ""
        });
      } else {
        this.updataPass();
      }
    },
    //
    updataPass() {
      // element
      var _this = this;
      this.$confirm("", "", {
        showCancelButton: true,
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        //
        .then(() => {
          // debugger;
          // var strs = _this.baseUrl;
          // console.log(strs);
          Axios.post("/HttpUtil/accountManager.ashx",
            {
              user: _this.user,
              action: "changePassword",
              oldPass: _this.passForm.oldPass,
              newPass: _this.passForm.newPass
            },

            {
              headers: { "Content-Type": "application/x-www-form-urlencoded" }
            }
          )
            // infodata   computed
            .then(response => (_this.updata = response.data))
            //
            .catch(function(error) {
              alert(error);
            });
        })
        //
        .catch(() => {
          // 
        });
    },
    //
    cancel() {
      this.$router.go(-1);
    }
  }
};
</script>
<style>
/*  */

.width {
  width: 300px;
}
.overallLayout {
  padding: 20px;
}
</style>



