/*
 * js
 * @create liurongtang 2018/07/08
*/
import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import HomeContent from './views/HomeContent.vue'
import AgentContent from './views/AgentContent.vue'
import Main from './views/Main.vue'
import Table from './views/nav1/Table.vue'
import Form from './views/nav1/Form.vue'
import user from './views/nav1/user.vue'
import echarts from './views/charts/echarts.vue'
import UserForms from './views/UserForms.vue'
import DlsUserForms from './views/UserForms.vue'
import NewForm from './views/NewForm.vue'
import Notice from './views/Notice.vue'
import AgentHome from './views/AgentHome.vue'
import AgentManager from './views/AgentManager.vue'
import materialMaintenance from './views/materialMaintenance.vue'
import materialInformation from './views/materialInformation.vue'
import Reports from './views/Reports.vue'
import ConditionQuotation from './views/ConditionQuotation.vue'
import changePassword from './views/changePassword.vue'
import Homepage from './views/Homepage.vue'
import DlschangePassword from './views/changePassword.vue'

let routes = [
    {
        path: '/login/:tname',
        component: Login,
        name: '',
        hidden: true,
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'workMenuIcon',//class
        children: [
            { path: '/Notice/:noticeType/:noticeStatus/:searchStr', component: Notice, name: '-', hidden: true, statisUrl:'/Notice///0' },
            { path: '/Notice/:noticeType/:noticeStatus/:searchStr', component: Notice, name: '-', hidden: true, statisUrl:'/Notice///0' },
            { path: '/Notice/:noticeType/:noticeStatus/:searchStr', component: Notice, name: '', hidden: true, statisUrl:'/Notice//0/0' },
            { path: '/Notice/:noticeType/:noticeStatus/:searchStr', component: Notice, name: '', hidden: true, statisUrl:'/Notice//0/0' },
            { path: '/NewForm/:name/:id/:nr', component: NewForm, name: '', hidden: true, statisUrl:'/NewForm//0/0' },
            { path: '/main', component: Main, name: '', hidden: true, statisUrl:'/main' },
            { path: '/HomeContent', component: HomeContent, name: '', hidden: true, statisUrl:'/HomeContent' },
           
            { path: '/agentmanager/:fromId', component: AgentManager, name: '', statisUrl:'/agentmanager/0' },
            { path: '/ConditionQuotation/:id', component: ConditionQuotation, name: '', statisUrl:'/ConditionQuotation/0' },
            { path: '/changePassword', component: changePassword, name: '', statisUrl:'/changePassword' },
            { path: '/userforms/:viewname/:isShowSearch/:searchStr', component: UserForms, name: '', statisUrl:'/userforms//0/0' }, //statisUrl URL
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'workMenuIcon',//class
        children: [
            { path: '/materialMaintenance', component: materialMaintenance, name: '', statisUrl:'/materialMaintenance',
            children:[
                { path: '/materialMaintenance/NewForm/:name/:id/:nr', component: NewForm, name: '', statisUrl:'/materialMaintenance/NewForm//0/0'},
            ]
           },
           { path: '/materialInformation', component: materialInformation, name: '', statisUrl:'/materialInformation',
           children:[
               { path: '/materialInformation/userforms/:viewname/:isShowSearch/:searchStr', component: UserForms, name: '', statisUrl:'/userforms//1/WZFLMC='},
           ]
          },
          { path: '/materialInformation', component: materialInformation, name: '', statisUrl:'/materialInformation',
          children:[
              { path: '/materialInformation/NewForm/:name/:id/:nr', component: NewForm, name: '', statisUrl:'/materialInformation/NewForm//0/0'},
          ]
         },
          { path: '/userforms/:viewname/:isShowSearch/:searchStr', component: UserForms, name: '', statisUrl:'/userforms//0/0' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'accountMenuIcon',
        children: [
            { path: '/table', component: Table, name: 'Table', statisUrl:'/table' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'recieveMenuIcon',
        children: [
            { path: '/userforms/:viewname/:isShowSearch/:searchStr', component: UserForms, name: '', statisUrl:'/userforms//0/0' },
            { path: '/userforms/:viewname/:isShowSearch/:searchStr', component: UserForms, name: '', statisUrl:'/userforms//0/0' }, 
            { path: '/userforms//:isShowSearch/:searchStr', template: UserForms, name: '', statisUrl:'/userforms//0/0' },
            { path: '/userforms//:isShowSearch/:searchStr', component: UserForms, name: '', statisUrl:'/userforms//0/0' },
            { path: '/userforms//:isShowSearch/:searchStr', component: UserForms, name: '', statisUrl:'/userforms//0/0' },
            { path: '/userforms//:isShowSearch/:searchStr', component: UserForms, name: '', statisUrl:'/userforms//0/0' }
    ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'loginMenuIcon',
        children: [
            { path: '/user', component: user, name: '', statisUrl:'/user' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'serviceMenuIcon',
        children: [
            { path: '/form', component: Form, name: 'Form', statisUrl:'/form' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'chartsMenuIcon',
        children: [
            { path: '/echarts', component: echarts, name: 'echarts', statisUrl:'/echarts'},
            { path: '/Reports/:reponame', component: Reports, name: '', statisUrl:'/Reports'}
        ]
    },
    {
        path: '/AgentHome',
        component: AgentHome,
        name: '',
        iconCls: 'workMenuIcon',
        children: [
            { path: '/dlsuserforms/:viewname/:isShowSearch/:searchStr', 
              component: DlsUserForms, 
              name: '', 
              statisUrl:'/dlsuserforms//0/0'
            },
            { path: '/AgentContent', component: AgentContent, name: '', hidden: true, statisUrl:'/AgentContent' },
            { path: '/Homepage', component: Homepage, name: '', hidden: true, statisUrl:'/Homepage' },
            { path: '/dlschangePassword', component: DlschangePassword, name: '', hidden: true, statisUrl:'/dlschangePassword' },
        ]
    },
    {
        path: '/AgentHome',
        component: NewForm,
        name: '',
        iconCls: 'workMenuIcon',//class
        children: [
            {
                path: '/materialInformation2',
                component: materialInformation,
                name: '',
                statisUrl: '/materialInformation'
            }]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];
export default routes;


