  //
  watch: {
    //
    $route(to, from) {
      this.nr = this.$route.params.nr;
      if (this.nr != "0") {
        this.Title = to.params.name;
        this.ID = to.params.id;
        this.quotationJump();
        this.getTemplateContentHandler();
      } else {
        //this.name 
        this.Title = to.params.name;
        this.ID = to.params.id;
        this.getTemplateContentHandler();
      }
    },
    //
    info: function() {
      var _this = this;
      // this.info
      _this.$nextTick(function() {
        //input
        this.verification();
      });
    }
