  computed: {
    //
    infodata: {
      get: function() {
        //get 
        return this.infodata;
      },
      // set  infpdata
      set: function(res) {
        // false alert 
        if (res.success == false) {
          this.$alert(":" + res.message, {
            confirmButtonText: ""
          });
        } else {
          if (this.jumpBJB == 1) {
            this.$alert(",", {
              confirmButtonText: ""
            }).then(() => {
              var url = "/NewForm//0/" + res.dataID;
              this.$router.push({ path: url });
            });
          } else {
            //  
            this.$alert("", {
              confirmButtonText: ""
            }).then(() => {
              // 
              this.cancel();
            });
          }
        }
      }
    },

    //
    WZBHData: {
      get: function() {
        //get 
        return this.WZBHData;
      },
      set: function(res) {
        //false alert 
        if (res.success == false) {
          this.$alert(":", {
            confirmButtonText: ""
          });
          return;
        } else {
          if (this.Title == "" || this.Title == "") {
            var TSRQ;
            if (this.Title == "") {
              TSRQ = $("input[name=TSRQ]").val();
            } else {
              TSRQ = $("input[name=TSRQ]").val();
            }
            var ZBYS = parseInt(this.HQXSJValue.ZBQ);
            var date = new Date(Date.parse(TSRQ.replace(/-/g, "/"))); //Data();
            var givenMonth = date.getMonth();
            var newMonth = givenMonth + ZBYS;
            date.setMonth(newMonth);
            var curdate = this.rfdateFormt("Y-m-d",date);
            this.$refs.refName.addviewdata(this.HQXSJValue, res.TSF, curdate);
          } else {
            this.$refs.refName.addviewdata(this.HQXSJValue, res.XSJ, 0);
          }
        }
      }
    },

    //
    infonew: {
      get: function() {
        //get 
        return this.infonew;
      },
      set: function(res) {
        //false alert 
        if (res.success == false) {
          this.$alert(":" + res.message, {
            confirmButtonText: ""
          });
          return;
        }
        //info
        if (this.Title == "") {
          this.processSubmission = false;
        } else {
          this.processSubmission = !this.processSubmission;
        }
        this.info = res.templateContent;

      }
    },

    infoFormData: {
      //
      get: function() {
        //get 
        return this.infoFormData;
      },
      set: function(res) {
        //false alert 
        if (res.success == false) {
          this.$alert(":" + res.message, {
            confirmButtonText: ""
          });
          return;
        }
        //info
        this.transEventlength = res.transEvent.length;
        if (res.transEvent.length == 0) {
          if (this.Title == "") {
            this.processSubmission = false;
          } else {
            this.processSubmission = !this.processSubmission;
          }
        } else if (this.ID != "0") {
          if (this.transEventlength == 1) {
            this.YJTX = !this.YJTX;
          }
          this.transEvent = res.transEvent;
          this.processRecords = res.processRecords;
        }

        this.editField = res.editField;
        this.mustField = res.mustField;
        this.info = res.templateContent;
        this.formdata = res.formData;
        this.getValue();
      }
    },

    quotationJumpDAta: {
      //
      get: function() {
        //get 
        return this.quotationJumpDAta;
      },
