            istime: true,
            choose: function(dates) {
              // 
              console.log(dates);
            }
          });
        }
        //
        else if ($(this).attr("_controltype") == "ExtDateMonth") {
          this.value = "";
          $(this).attr("id", this.name); //ID

          laydate({
            //
            elem: "#" + this.name,
            format: "YYYY-MM-DD", // 
