          alert(error);
        });
    },
    getJXSList(val) {
      var _this = this;
      Axios.post(
        this.baseUrl + "HttpUtil/accountManager.ashx", //
        {
          //  username
          action: "GetDlsJxs",
          type: "2",
          dls: val
        },
        { headers: { "Content-Type": "application/x-www-form-urlencoded" } }
      )
        // info
        .then(function(res) {
          $("[name=JXSSPAN]").show();
          $("[name=JXS]").show();
          var data = res.data;
          var jxs = "";
          if (_this.ID > 0) jxs = _this.formdata[0].JXS;

          if (data.code == "success") {
            var optionStr = "<option disabled selected hidden></option>";
            for (var i = 0; i < data.data.length; i++) {
              if (_this.ID > 0 && jxs == data.data[i].GSMC) {
                optionStr +=
                  '<option selected value="' +
                  data.data[i].GSMC +
                  '">' +
                  data.data[i].GSMC +
                  "</option>";
              } else {
                optionStr +=
                  '<option value="' +
                  data.data[i].GSMC +
                  '">' +
                  data.data[i].GSMC +
                  "</option>";
              }
            }
            $("[name=JXS]").html(optionStr);
          }
        })
        .catch(function(error) {
          alert(error);
        });
    },
    onDxsglGslx() {
      var _this = this;
      $("#GsleSele").on("change", function() {
        var val = $("#GsleSele").val();
        if (val == "") {
          $("[name=DLSU]").show();
          $("[name=DLS]").show();
          _this.getDlSList2();
        } else {
          $("[name=DLSU]").hide();
          $("[name=DLS]").hide();
        }
      });
    },
    getDlSList2() {
      var _this = this;
      Axios.post(
        this.baseUrl + "HttpUtil/accountManager.ashx", //
        {
          //  username
          action: "GetDlsJxs",
          type: "1"
        },
        { headers: { "Content-Type": "application/x-www-form-urlencoded" } }
      )
        // info
        .then(function(res) {
          var data = res.data;
          if (data.code == "success") {
            var optionStr = "<option disabled selected hidden></option>";
            for (var i = 0; i < data.data.length; i++) {
              optionStr +=
                '<option value="' +
                data.data[i].GSMC +
                '">' +
                data.data[i].GSMC +
                "</option>";
            }
            $("[name=DLS]").html(optionStr);
          }
        })
        .catch(function(error) {
          alert(error);
        });
    },
    getUUID() {
      return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function(
        c
      ) {
        var r = (Math.random() * 16) | 0,
          v = c == "x" ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      });
    },
