<!--
 * 
 * @create liurongtang 2018/07/08
-->
<template>
  
  <div class="container-login">
    <div>
          <img src="../assets/loginBg.jpg" class="bgmImg"/>
    </div>
    <div class="container">
      <el-form
        :model="userInputFormData"
        :rules="userInputRules"
        ref="userInputFormData"
        label-position="left"
        label-width="0px"
        class="demo-ruleForm login-container"
      >
        <el-form-item style="width:100%;text-align:center;">
          <!-- <img src="../assets/loginTittle.png" style="width:98%" /> -->
          <a class="loginTiittle"></a>
        </el-form-item>

        <el-form-item prop="account" class="account-login">
          <i class="icon-user-input"></i>
          <el-input
            type="text"
            v-model="userInputFormData.account"
            auto-complete="off"
            class="login-user-input"
            id="loginUserInput"
            placeholder=""
            @input="checkInput"
          ></el-input>
          <i class="icon-clean-account" @click="cleanAccountInput"></i>
        </el-form-item>
        <el-form-item prop="checkPass" class="password-login">
          <i class="icon-password-input"></i>
          <el-input
            type="password"
            v-model="userInputFormData.checkPass"
            auto-complete="off"
            class="login-password-input"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item style="width:100%;">
          <el-button
            type="primary"
            style="width:100%;height:55px;font-size:25px;margin-top:95px;"
            @click.native.prevent="handleSubmit"
            
          ></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { requestLogin } from "../api/api";
import Axios from "Axios";
import echarts from '../views/charts/echarts.vue'
import NewForm from '../views/NewForm.vue'
import Home from '../views/Home.vue'
import materialInformation from '../views/materialInformation.vue'
import materialMaintenance from '../views/materialMaintenance.vue'
import UserForms from '../views/UserForms.vue'
import AgentManager from '../views/AgentManager.vue'
import $ from "jquery";

//
export default {
  created() {
    var _self = this
    document.onkeydown = function(e) {
      var key = window.event.keyCode;
      if (key === 13) {
        _self.handleSubmit()
      }
    }

  },
  mounted: function(){
    if(this.tname == '1'){
      $(".loginTiittle").append('');
    }
    else if(this.tname == '2'){
      $(".loginTiittle").append('');
    }
    else if(this.tname == '3'){
      $(".loginTiittle").append('');
    }
    else if(this.tname == '4'){
      $(".loginTiittle").append('');
    }

  },
  data() {
    return {
      logining: false,
      userInputFormData: { //json
      account: "",
      checkPass: ""
      },
      userInputRules: { //json
        account: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      checked: true,
      tname: this.$route.params.tname,
    };
  },
  methods: {
    //
    handleSubmit(ev) {
      var _this = this;
      this.$refs.userInputFormData.validate(valid => {//
        if (valid) {
          this.logining = true;
          var loginParams = {
            username: this.userInputFormData.account,
            password: this.userInputFormData.checkPass
          };
          Axios.post(this.baseUrl +"HttpUtil/LoginValidateHandler.ashx", //ajax 
            loginParams,
            {
                headers: {'Content-Type': 'application/x-www-form-urlencoded'} //
            }
          )
          // res
          .then( res => {
            console.log("");
            console.log(res.data);
            console.log(res.data.success);
            //
            if(res&&res.data&&res.data.success){
              var user='{"name":"'+$.trim(this.userInputFormData.account)+'"}';//json
              sessionStorage.setItem("user", JSON.stringify(user));//session
              sessionStorage.setItem("curusercode",res.data.curusercode);
              sessionStorage.setItem("curusername",res.data.curusername);
              sessionStorage.setItem("curdepartment",res.data.curdepartment);
              sessionStorage.setItem("curRole",res.data.curRole);
              sessionStorage.setItem("agent",res.data.agent);
              sessionStorage.setItem("dealer",res.data.dealer);
              sessionStorage.setItem("engineer",res.data.engineer);
              //modify by liulinqing 20190716
              //this.initeMenu($.trim(this.userInputFormData.account));//
              //title
              
              if(res.data.curdepartment == ""){
                  this.$router.push("/");//
                  $(document).attr("title","");
              }else{
                  this.$router.push("/AgentHome");   //
              }
              //this.$router.push("/HomeContent"); //
            }else{
              this.$message({//
                  message: res.data.message,//
                  type: "error"
              });
              console.log("");
            }
          }) 
          .catch(function(error) {
            console.log(error);
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //
    initeMenu(userAccount) {
        var initPath="";
        Axios.post(this.baseUrl +"HttpUtil/GetLeftMenuHandler.ashx", //ajax 
          {username:userAccount},
          {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'} //
          }
        )
        // res
        .then( res => {
          //
          if(res&&res.data&&$.trim(res.data.success)==true){
            var dataJson= res.data;//
            var errorJson={
              path: '*',
              hidden: true,
              redirect: { path: '/404' }
            };
            for(var i=0;i<dataJson.data.length;i++){//
            var addRoutesJson={//
              path:'/',
              children:[] 
            };
              addRoutesJson.component=Home;//home
              addRoutesJson.name=dataJson.data[i].moduleTitle;//
              addRoutesJson.iconCls=dataJson.data[i].iconCls;//
              for(var j=0;j<dataJson.data[i].submenu.length;j++){//
                 var addRoutesInnerJson={};//json
                 if(dataJson.data[i].submenu[j].component=="materialMaintenance"){
                    addRoutesInnerJson.path="/materialMaintenance";//
                    addRoutesInnerJson.component=materialMaintenance;//newform
                    addRoutesInnerJson.name=dataJson.data[i].submenu[j].text;//
                    addRoutesInnerJson.statisUrl=dataJson.data[i].submenu[j].url;//
                    if(i==0&&j==0){
                      initPath=dataJson.data[i].submenu[j].url;
                    }
                 }
                 addRoutesJson.children.push(addRoutesInnerJson);// childrenjson             
              }
              const addRoutes=[addRoutesJson];//json
              this.$router.addRoutes(addRoutes);//
              this.$router.options.routes.push(addRoutesJson);//routesjson
            }
            const errorRoutes=[errorJson];//404json
            this.$router.addRoutes(errorRoutes);//404
            this.$router.options.routes.push(errorJson);//routes404json
            this.$router.push({ path: initPath });//
          }else{
             console.log("");
          }
        }) 
        .catch(function(error) {
          console.log(error);
        });        
      
    },
    //
    checkInput(val) {
      if (val && "" != $.trim(val)) {
        $(".icon-clean-account").css("visibility", "visible");
      }
      if ("" == $.trim(val)) {
        $(".icon-clean-account").css("visibility", "hidden");
      }
    },
    //
    cleanAccountInput() {
      this.userInputFormData.account = "";
      $(".icon-clean-account").css("visibility", "hidden");
    }
  }
};

</script>

<style lang="scss">
.container-login .container {
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100%;
  height: 100%;
  //background-image: url(../assets/loginBg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}
.login-container {
  margin-left: 70%;
  margin-top: 16%;
  width: 20%;
  padding: 35px 35px 15px 35px;
  background: transparent;
  .title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
  .remember {
    margin: 0px 0px 35px 0px;
  }
}

.icon-user-input {
  position: absolute;
  left: 4%;
  top: 70%;
  background-image: url(../assets/userIcon.png);
  background-size:24px 30px;
  background-repeat: no-repeat;
  background-position: 0px 3px;
  width: 24px;
  height: 35px;
  z-index: 777;
}
.icon-clean-account {
  float: right;
  position: absolute;
  right: 4%;
  top: 80%;
  background-image: url(../assets/loginCleanIcon.png);
  background-size:25px 25px;
  background-repeat: no-repeat;
  background-position: 5px 3px;
  width: 31px;
  height: 30px;
  z-index: 888;
  visibility: hidden;
}
.icon-password-input {
  position: absolute;
  left: 4%;
  top: 160%;
  background-image: url(../assets/passWordIcon.png);
  background-size:24px 30px;
  background-repeat: no-repeat;
  background-position: 0px 3px;
  width: 24px;
  height: 35px;
  z-index: 999;
}

.login-user-input .el-input__inner {
  padding-left: 50px;
  position: absolute;
  font-size: 18px;
  border: 0px;
  z-index: 333;
  height:50px;
  width: 100%;
  margin-top: -5px;
  background: url(../assets/loginInputBg.png);
  background-repeat: no-repeat;
  background-size: cover;
}

.login-password-input .el-input__inner {
  padding-left: 51px;
  position: absolute;
  font-size: 18px;
  border: 0px;
  height:50px;
  z-index: 333;
  margin-top: 28px;
  width: 100%;
  background: url(../assets/loginInputBg.png);
  background-repeat: no-repeat;
  background-size: cover;
}

.account-login .el-form-item__error {
  display:none;
  font-size: 15px;
  top: 65px;
}

.password-login .el-form-item__error {
  font-size: 15px;
  display:none;
  top: 98px;
}

.bgmImg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
}
.loginTiittle{
    font-weight: 600;
    font-size: 25px;
    font-family: Helvetica;
    color: #303030;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -khtml-user-select: none;
    user-select: none;

}
</style>


